<template>
  <div class="editor">
    <div v-if="this.token.length !== 0" class="editor-wrapper">
      <div class="editor-item">
        <span @click="selectIdea" class="editor-idea-icon"></span>
        <span class="editor-name"></span>
      </div>
      <div class="editor-item">
        <span @click="selectWorks" class="editor-works-icon"></span>
        <span class="editor-name"></span>
      </div>
      <div class="editor-item">
        <span @click="selectPaper" class="editor-paper-icon"></span>
        <span class="editor-name"></span>
      </div>
      <div class="editor-item">
        <span @click="selectData" class="editor-data-icon"></span>
        <span class="editor-name"></span>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import { mapGetters } from 'vuex'
  import { addIdea, addWorks, addPaper, addData } from 'api/philosopher'

  export default {
    computed: {
      ...mapGetters([
        'token',
        'timestamp',
        'uid',
        'philosopher'
      ])
    },
    methods: {
      selectIdea() {
        this.$swal.setDefaults({
          input: 'text',
          confirmButtonText: 'Next &rarr;',
          showCancelButton: true,
          animation: false,
          progressSteps: ['1', '2', '3', '4']
        })
        let steps = [
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: '/'
          },
          {
            title: '',
            text: ' 0.0~5.0'
          }
        ]
        this.$swal.queue(steps).then((result) => {
          this.$swal.resetDefaults()
          console.log(result)
          let params = {
            uid: this.uid,
            token: this.token,
            timestamp: this.timestamp,
            pid: this.philosopher.id,
            content: result[0],
            frombook: result[1],
            frompaper: result[2],
            rating: result[3]
          }
          addIdea(params).then(res => {
            console.log(res)
            if (res.status === 0) {
              this.$swal('', '', 'success')
            }
          })
        }, function () {
          this.$swal.resetDefaults()
        })
      },
      selectWorks() {
        this.$swal.setDefaults({
          input: 'text',
          confirmButtonText: 'Next &rarr;',
          showCancelButton: true,
          animation: false,
          progressSteps: ['1', '2', '3']
        })
        let steps = [
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ' 0.0~5.0'
          }
        ]
        this.$swal.queue(steps).then((result) => {
          this.$swal.resetDefaults()
          console.log(result)
          let params = {
            uid: this.uid,
            token: this.token,
            timestamp: this.timestamp,
            pid: this.philosopher.id,
            worksname: result[0],
            worksurl: result[1],
            rating: result[2]
          }
          addWorks(params).then(res => {
            console.log(res)
            if (res.status === 0) {
              this.$swal('', '', 'success')
            }
          })
        }, function () {
          this.$swal.resetDefaults()
        })
      },
      selectPaper() {
        this.$swal.setDefaults({
          input: 'text',
          confirmButtonText: 'Next &rarr;',
          showCancelButton: true,
          animation: false,
          progressSteps: ['1', '2', '3', '4']
        })
        let steps = [
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ' 0.0~5.0'
          }
        ]
        this.$swal.queue(steps).then((result) => {
          this.$swal.resetDefaults()
          let params = {
            uid: this.uid,
            token: this.token,
            timestamp: this.timestamp,
            pid: this.philosopher.id,
            papername: result[0],
            author: result[1],
            paperurl: result[2],
            rating: result[3]
          }
          addPaper(params).then(res => {
            console.log(res)
            if (res.status === 0) {
              this.$swal('', '', 'success')
            }
          })
        }, function () {
          this.$swal.resetDefaults()
        })
      },
      selectData() {
        this.$swal.setDefaults({
          input: 'text',
          confirmButtonText: 'Next &rarr;',
          showCancelButton: true,
          animation: false,
          progressSteps: ['1', '2', '3']
        })
        let steps = [
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ''
          },
          {
            title: '',
            text: ' 0.0~5.0'
          }
        ]
        this.$swal.queue(steps).then((result) => {
          this.$swal.resetDefaults()
          console.log(result)
          let params = {
            uid: this.uid,
            token: this.token,
            timestamp: this.timestamp,
            pid: this.philosopher.id,
            name: result[0],
            url: result[1],
            rating: result[2]
          }
          addData(params).then(res => {
            console.log(res)
            if (res.status === 0) {
              this.$swal('', '', 'success')
            }
          })
        }, function () {
          this.$swal.resetDefaults()
        })
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"

  .editor
    display flex
    position absolute
    top 50px
    left 0
    bottom 0
    width 100%
    background #f3f5f7
    .editor-wrapper
      display flex
      flex-direction row
      margin 30px auto
      .editor-item
        width 120px
        height 200px
        text-align center
        margin auto 20px
        .editor-idea-icon
          display block
          margin 20px auto
          width 100px
          height 100px
          bg-image('talk')
          background-size 100px 100px
          background-repeat no-repeat
          &:hover
            width 110px
            height 110px
            background-size 110px 110px
            background-repeat no-repeat
        .editor-works-icon
          display block
          margin 20px auto
          width 100px
          height 100px
          bg-image('book')
          background-size 100px 100px
          background-repeat no-repeat
          &:hover
            width 110px
            height 110px
            background-size 110px 110px
            background-repeat no-repeat
        .editor-paper-icon
          display block
          margin 20px auto
          width 100px
          height 100px
          bg-image('paper')
          background-size 100px 100px
          background-repeat no-repeat
          &:hover
            width 110px
            height 110px
            background-size 110px 110px
            background-repeat no-repeat
        .editor-data-icon
          display block
          margin 20px auto
          width 100px
          height 100px
          bg-image('data')
          background-size 100px 100px
          background-repeat no-repeat
          &:hover
            width 110px
            height 110px
            background-size 110px 110px
            background-repeat no-repeat
        .editor-name
          font-size 14px
          font-family "Songti SC"

</style>



